//import axios from "axios";
import Customs from "./customService.js";
import swal from "sweetalert";
import SocietyPersonal from '../SocietyPersonal';
//import Clients from '../Clients';

export default {
    name: 'ProjectAdd',
    components: {
        SocietyPersonal,
    },
    data: () => ({
        mounted: false,
        userId: "",
        newProject: {
            name: "",
            description: "",
            creatorId: "",
            societyId: "",
        },
    }),

    methods: {
        async addProject() {
            try {
                this.newProject.societyId = this.$refs.society.selectedId;
                await this.$http
                    .post("/project/add", {
                        project: this.newProject,
                    })
                    .then(response => {
                        swal("Success", response.data.message, "success");
                        this.$emit('custom-event-name', {isAdd: true});
                        console.log('send');
                        return (response);
                    });
                this.newProject.name = '';
                this.newProject.description = '';
            }
            catch (err) {
                let error = err.response;
                if (error.status == 409) {
                swal("Error", error.data.message, "error");
                } else {
                swal("Error", error.data.err.message, "error");
                }
            }
        },
    },

    created() {
        let user = Customs.getUserDetails();
        this.userId = user._id;
        this.newProject.creatorId = user._id;
        //this.getMyClients();
    },

    updated() {
    },

    mounted(){
        this.mounted = true;
    }
};