<template>
<div>
  <navbar></navbar>
  <div class="project">
    <projectAdd @custom-event-name="projectAdded" />
    <projectsPersonal ref="projectsPersonal" />
  </div>
  </div>
</template>
<script>
import navbar from "@/components/Navbar";
import projectAdd from "@/components/ProjectAdd";
import projectsPersonal from "@/components/ProjectsPersonal";

export default {
  components: {
    projectAdd,
    projectsPersonal,
    navbar
  },
  methods: {
    projectAdded (event) {
      if (event.isAdd) {
        this.refreshed();
      }
    },

    refreshed() {
      this.$refs.projectsPersonal.getUserProjects();
    }
  }
};
</script>